.post {
    position: relative;

    padding-left: 100px;
    margin-bottom: 50px;

    font-size: 20px;
}

.post::before {
    content: '';
    position: absolute;
    top: -15px;
    left: 20px;
    width: 50px;
    height: 50px;
    background-color: #ffd000;
    border-radius: 50%;
}